<template>
    <div class="xrr">
        <audio src="http://www.ytmp3.cn/down/50494.mp3" loop="loop" autoplay="autoplay">
            Your browser does not support the audio element.
        </audio>
        <p class="x0">你一定很好奇这是什么吧？</p>
        <p class="x1">请往下看...</p>
        <img src="@/assets/xrr/pointer.gif" alt="">

        <img class="dinner" src="@/assets/xrr/xrr1.jpg" alt="">
        <p class="x2">难道这就是传说中的兔女郎么？</p>
        <img class="inter" src="@/assets/xrr/inter.gif" alt="">
        <p class="who">是谁是谁你是谁....</p>

        <p class="other">
            让我们再来看一组照片
        </p>

        <img class="dinner" src="@/assets/xrr/xrr2.jpg" alt="">
        <img class="dinner" src="@/assets/xrr/xrr3.jpg" alt="">
        <img class="dinner" src="@/assets/xrr/xrr4.jpg" alt="">
        <img class="dinner" src="@/assets/xrr/xrr5.jpg" alt="">
        <img class="dinner last" src="@/assets/xrr/xrr6.jpg" alt="">
        <p class="x10">哈哈.....这不就是傻叉徐蓉蓉嘛，OY！！</p>


        <div class="textArea">
            <p class="extra">附:</p>
            <p class="homework">
                我的作品还算完美吧，记住不要喷我，哈哈...
            </p>
        </div>
        <!--<img class="inter" src="/static/images/xrr/inter.gif" alt="">-->
        <!--<p class="who">是谁是谁你是谁....</p>-->

         <table class="table">
             <tr>
                 <th>姓名</th>
                 <th>年龄</th>
             </tr>
             <tr v-for="(item,index) in db" :key = index>
                 <td>{{ item.name }}</td>
                 <td>{{ item.age }}</td>
             </tr>
         </table>

    </div>
</template>
<script>
    export default {
        name: "xrr",
        data(){
            return {
                db:[],
            }
        },
        created(){
            this.$axios.get('/api/lottery/d11x5/recent_list', {
                params:{
                    lottery_id:201,
                }
            }).then(res => {
                let data = res.data;
                if (data.code === 200) {
                    console.log(data);
                } else {
                    console.log("接口请求错误");
                }
            }).catch(error => {
                console.log(error)
            });


          //数据库请求
            this.$axios.get('/mongodb/find', {
            }).then(res => {
                console.log(res);
                this.db = res.data;
            }).catch(error => {
                console.log(error)
            });
        }
    }
</script>

<style scoped lang="less">
    @import "../assets/css/vrem.less";
    .xrr{
        background-color: #fff;
        .toRem(padding-bottom,100);
    }
    .dinner{
        width:100%;
        .toRem(margin-bottom,30);
    }
    .last{
        margin-bottom:0;
    }
    .x0{
        .toRem(padding,60);
        .toRem(font-size,100);
        color:pink;
    }
    .x1{
        .toRem(font-size,60);
        text-align: center;
        color:#000000;
    }
    .x2{
        .toRem(font-size,50);
        .toRem(padding-left,40);
    }
    .x10{
        .toRem(font-size,50);
        text-align: center;
        .toRem(line-height,100);
        color:pink;
    }
    .inter{
        width:50%;
        .toRem(margin-top,30);
        margin-left:50%;
        transform:translateX(-50%);
    }
    .who{
        text-align: center;
        .toRem(font-size,50);
        .toRem(margin-top,20);

    }
    .other{
        .toRem(font-size,50);
        .toRem(padding-left,40);
        .toRem(margin-top,80);
        .toRem(margin-bottom,50);
    }
    .textArea{
        width:90%;
        margin:0 auto;
        .toRem(margin-top,100);
        /*<!--.toRem(margin-bottom,100);-->*/
        .toRem(height,180);
        border:1px solid lightBlue;
        .extra{
            .toRem(font-size,30);
            .toRem(line-height,80);
            .toRem(padding-left,40);
        }
        .homework{
            .toRem(padding-left,40);
            .toRem(font-size,30);
        }
    }
    .table{
        .toRem(font-size,30);
        width:96%;
        margin:0 auto;
        .toRem(margin-top,40);
        border: 1px solid black;
        border-collapse: collapse;
        td{
            border:1px solid black;
            .toRem(line-height,80);
            text-align:center;
        }
        th{
            .toRem(line-height,80);
        }

    }
</style>


